    populateData() {
        const getdata = `
        query {
            HouseRegisters {    
              REG_HOUSE_ID
               REG_TENANT_NUMBER
               HOUSE_NUMBER
               SET_ESTATE_ID
               ESTATEDETAILS{
                   ESTATE_ID
                   ESTATE_NAME
               }
               SET_HOUSE_TYPE_ID
               HOUSETYPE{
                   HOUSE_TYPE_ID
                   HOUSE_TYPE
               }
           SET_MODULE_ID
               MODULES{
               MODULE_ID
           MODULE_NAME
           }
           
           SET_DEPT_FEECATEGORY_ID
               FEECATEGORY{
                   FEECATEGORY_ID
                   FEECATEGORY_CODE
               }
               SET_DEPT_FEE_ID
               FEE{
                   FEE_ID
                   FEE_NAME
               }
           
               CLIENT_ID
               CLIENT{
                   CLIENT_ID
                   CLIENT_NAME
               }
               SYSTEMSTATUS{
                   SYS_STATUS_ID
                   SYS_STATUS_NAME
               }
               CREATEDBY
               MODIFIEDBY
               CREATED_DATE
               MODIFIED_DATE
            }
           }
           
           
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.HouseRegisters
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })
        console.log(getdata);
    }

