    handleSubmit = (event) => {
        event.preventDefault();
        const form = `
        mutation{
            addHouseRegister(
                REG_TENANT_NUMBER: "${this.state.REG_TENANT_NUMBER}",
                HOUSE_NUMBER:${this.state.HOUSE_NUMBER}
                SET_ESTATE_ID: ${this.state.ESTATE_ID},
                SET_HOUSE_TYPE_ID:${this.state.HOUSE_TYPE_ID}
                SET_MODULE_ID: ${this.state.SET_MODULE_ID},                                    
                SET_DEPT_FEECATEGORY_ID:${this.state.SET_DEPT_FEECATEGORY_ID},
                SET_DEPT_FEE_ID:${this.state.SET_DEPT_FEE_ID},
                CLIENT_ID:${this.state.CLIENT_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CREATEDBY:"${this.state.CREATEDBY}"
                MODIFIEDBY:"${this.state.MODIFIEDBY}"
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    REG_HOUSE_ID
            }
         }
        `;

        const formEdit = `
        mutation{
            updateHouseRegister(
                REG_HOUSE_ID:${this.state.REG_HOUSE_ID},
                REG_TENANT_NUMBER: "${this.state.REG_TENANT_NUMBER}",
                HOUSE_NUMBER:${this.state.HOUSE_NUMBER}
                SET_ESTATE_ID: ${this.state.SET_ESTATE_ID},
                SET_HOUSE_TYPE_ID:${this.state.SET_HOUSE_TYPE_ID}
                SET_MODULE_ID: ${this.state.SET_MODULE_ID},                                    
                SET_DEPT_FEECATEGORY_ID:${this.state.SET_DEPT_FEECATEGORY_ID},
                SET_DEPT_FEE_ID:${this.state.SET_DEPT_FEE_ID},
                CLIENT_ID:${this.state.CLIENT_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CREATEDBY:"${this.state.CREATEDBY}"
                MODIFIEDBY:"${this.state.MODIFIEDBY}"
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    REG_HOUSE_ID
            }
         }
        `;

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(({ data }) => {

                    let houseRegID = data.data.addHouseRegister.REG_HOUSE_ID;

                    let reg_house_num = this.state.DOCUMENT_PREFIX + '-' + parseInt(houseRegID + this.state.INCREMENTBY)

                    this.setState({
                        REG_TENANT_NUMBER: reg_house_num
                    });
                    this.GenerateDocumentNumber(houseRegID);

                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }

    }

