    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =

                    item.ESTATEDETAILS.ESTATE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.HOUSETYPE.HOUSE_TYPE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MODULES.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.FEECATEGORY_TYPE.FEECATEGORY_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.FEE.FEE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CLIENT.CLIENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYSTEM_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.REG_TENANT_NUMBER.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.HOUSE_NUMBER.toString().toLowerCase().startsWith(value.toLowerCase())


                let includesCondition =

                    item.ESTATEDETAILS.ESTATE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.HOUSETYPE.HOUSE_TYPE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MODULES.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.FEECATEGORY_TYPE.FEECATEGORY_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.FEE.FEE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CLIENT.CLIENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYSTEM_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.REG_TENANT_NUMBER.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.HOUSE_NUMBER.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }


    handleSearchEstateDetailsFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchestatedetailsdata
        let filteredEstateDetailsData = this.state.searchFilteredEstateDetailsData
        this.setState({ searchestatedetailsvalue: value });

        if (value.length) {
            filteredEstateDetailsData = data.filter(item => {
                let startsWithCondition =
                    item.ESTATE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.ESTATE_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredEstateDetailsData })
        }
    }

