import React from "react"
import { Button } from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather"
import Can from '../../../../../utility/permissions/can';

class HouseRegistrationTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Tenant Number",
                selector: "REG_TENANT_NUMBER",
                sortable: true,
                cell: row => <p className="mb-0">{row.REG_TENANT_NUMBER}</p>
            },

            {
                name: "ESTATE DETAILS",
                selector: "ESTATE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.ESTATEDETAILS.ESTATE_NAME}</p>
            },

            {
                name: "HOUSE TYPE",
                selector: "HOUSE_TYPE",
                sortable: true,
                cell: row => <p className="mb-0">{row.HOUSETYPE.HOUSE_TYPE}</p>
            },

            {
                name: "Client Name",
                selector: "CLIENT_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.CLIENT.CLIENT_NAME}</p>
            },

            {
                name: "House Number",
                selector: "HOUSE_NUMBER",
                sortable: true,
                cell: row => <p className="mb-0">{row.HOUSE_NUMBER}</p>
            },
            {
                name: "Module",
                selector: "MODULE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.MODULES.MODULE_NAME}</p>
            },

            {
                name: "Revenue Name",
                selector: "FEE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.FEE.FEE_NAME}</p>
            },

        ]
    }

    render() {

        return (
            <>
                <DataTable
                    className="dataTable-custom"
                    data={this.props.value.length ? this.props.filteredData : this.props.data}
                    columns={this.tableProperties.columns}
                    noHeader
                    pagination
                    subHeader
                    striped
                    responsive
                    subHeaderComponent={
                        <TableCustomHeader role={this.props.role} value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />

                    }
                />
            </>

        )
    }
}


export default HouseRegistrationTable